import axios from 'axios'

const domain = 'https://csc.wanvale.com/thcode/api'
// const domain = 'http://127.0.0.1:8789'

const getWithoutParams = (url) => {
    return axios.get(domain + url)
}

const getNodesById = (id) => {
    return getWithoutParams("/category/getNodesById/" + id)
}

const getCodeById = (id) => {
    return getWithoutParams("/code/getCodeById/" + id)
}

const exportedObject = {
    getNodesById,
    getCodeById
}

export default exportedObject